@-webkit-keyframes spin {
0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
}
}
@keyframes spin {
0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
}
}
.spinner-wrap {
  text-align: center;
}
.cheap-spinner {
  -webkit-animation: spin 2s infinite;
          animation: spin 2s infinite;
  border-radius: 50%;
  margin: 15px auto;
  border: 2px solid #e801ff;
  color: #e801ff;
  background: #020203;
  padding: 10px;
  display: inline-block;
  font-size: 3rem;
  width: 75px;
  height: 75px;
  position: relative;
}
.cheap-spinner p {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0;
  text-shadow: 1px 1px 2px #e801ff;
}

