.non_auth .btn_login {
  margin-left: auto;
}

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.modal {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
}
.modal .modal-content {
  background: white;
  position: relative;
  width: 50%;
  left: 25%;
  top: 25%;
  max-height: 75%;
  border-radius: 8px;
  color: #020203;
}
.modal .modal-header {
  min-height: 40px;
  border-bottom: 1px #d1d1d1 solid;
  padding: 5px 20px;
}
.modal .modal-footer {
  min-height: 40px;
  border-top: 1px #d1d1d1 solid;
  padding: 5px 20px;
}
.modal .modal-body {
  padding: 5px 20px;
}
.modal .modal-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.auth .btn_logout {
  margin-left: auto;
}

footer {
  background: #8a6a8e;
  padding: 2px 10px;
}

.welcome {
